<template>
  <el-row>
    <el-col class="login">
      <div class="login-page">
        <el-row :gutter="10">
          <el-col>
            <div class="head-pic">
              <img src="../assets/images/1-1/2.png" alt="">
            </div>
          </el-col>
          <el-col>
            <div class="login-list">
              <div class="item">
                <span>用户账号</span>
                <label class="login-label">
                  <el-input v-model="input" @click.native="change(1)" :class="{active:act_index===1}" class="login-input" @keyup.native="account"></el-input>
                  <p class="login-p" v-show="acc">请输入你的登录账号</p>
                </label>
              </div>
              <div class="item itema">
                <span>密码</span>
                <label class="login-label2">
                  <el-input v-model="psd" @click.native="change(2)" :class="{active:act_index===2}" class="login-input" @keyup.native="pwd"></el-input>
                  <p class="login-p2" v-show="password">请输入你的密码</p>
                </label>
              </div>
              <div class="item itema">
                <span>验证码</span>
                <div class="row">
                  <div class="left">
                    <label class="login-label3">
                      <el-input v-model="verification"  @click.native="change(3)" :class="{active:act_index===3}" class="login-input" @keyup.native="code"></el-input>
                      <p class="login-p3" v-show="auth">请输入右侧验证码</p>
                    </label>
                  </div>
                  <div class="right"></div>
                </div>
              </div>
            </div>
          </el-col>
          <el-col>
            <div class="login-button">
              <el-button native-type="button" @click="login">登录</el-button>
            </div>
          </el-col>
          <el-col>
            <div class="register">
              <span>没有账号?</span>
              <span>
                <router-link to="register">现在注册 </router-link>
              </span>
            </div>
          </el-col>
        </el-row>
      </div>
    </el-col>
  </el-row>

</template>

<script>
import "element-ui/lib/theme-chalk/display.css";
export default {
  name: "login",
  data() {
    return {
      input: "",
      psd: "",
      verification: "",
      act_index: 0,
      acc:true,
      password:true,
      auth:true
    };
  },
  methods: {
    change: function(index) {
      //  this.act_index =index
    },
    login: function() {
      this.$router.push({
        path: "/home" //跳转的路径
      });
    },
    account() {
      if (this.input !== "") {
        this.acc = false;
      }
      if (this.input === "") {
        this.acc = true;
      }
    },
      pwd() {
      if (this.psd !== "") {
        this.password = false;
      }
      if (this.psd === "") {
        this.password = true;
      }
    },
      code() {
      if (this.verification !== "") {
        this.auth = false;
      }
      if (this.verification === "") {
        this.auth = true;
      }
    },
  }
};
</script>

<style scoped>
</style>


<style >
.login {
  background: #5bba9d;
  height: 737px;
}
.login .login-page {
  width: 340px;
  display: block;
  background: #ffffff;
  border: #5bba9d 1px;
  border-radius: 6px;
  box-shadow: -2px 0px 14px rgba(73, 73, 73, 0.1);
  margin: 77px auto 0px auto;
}
.login .login-page .head-pic {
  width: 340px;
  height: 39px;
  padding-top: 29px;
  margin: 0 auto;
}
.login .login-page .head-pic img {
  width: 161px;
  height: 39px;
  margin: 0 auto;
  display: block;
}
.login .login-page .login-list {
  padding: 37px 40px 0px 40px;
}
.login .login-page .login-list .itema {
  padding-top: 20px;
}
.login .login-page .login-list .item span {
  color: #4d4d4d;
  font-size: 13px;
}
.login .login-page .login-list .item .el-input__inner {
  border: none;
  border-radius: 0px;
  border-bottom: 1px solid #e5e5e5;
  font-size: 13px;
  color: #1a1a1a;
  padding: 12px 0px;
  background: #ffffff;
  line-height: 13px;
}
.login
  .login-page
  .login-list
  .item
  .el-input__inner::-webkit-input-placeholder {
  font-size: 13px;
  color: #b3b3b3;
}
.login .login-page .login-list .item .el-input__inner:-moz-placeholder {
  color: #b3b3b3 !important;
  font-size: 13px;
  /* Mozilla Firefox 4 to 18 适配火狐 */
}
.login .login-page .login-list .item .el-input__inner::-moz-placeholder {
  color: #b3b3b3 !important;
  font-size: 13px;
  /* Mozilla Firefox 19+ 适配火狐 */
}
.login .login-page .login-list .item .el-input__inner:-ms-input-placeholder {
  color: #b3b3b3 !important;
  font-size: 13px;
  /* Internet Explorer 10+  适配ie*/
}
.login .login-page .login-list .item .row {
  display: flex;
}
.login .login-page .login-list .item .row .left {
  padding-right: 14px;
}
.login .login-page .login-list .item .row .right {
  width: 80px;
  height: 38px;
  background: #e5e5e5;
}
.login .login-page .login-button {
  padding-top: 25px;
}
.login .login-page .login-button .el-button {
  width: 260px;
  height: 38px;
  margin: 0 auto;
  display: block;
  border-radius: 4px;
  background: #5bba9d;
}
.login .login-page .login-button .el-button span {
  font-size: 15px;
  color: #ffffff;
}
.login .login-page .register {
  width: 100%;
  text-align: center;
  padding: 23px 0px 31px 0px;
}
.login .login-page .register span {
  font-size: 12px;
  color: #b3b3b3;
}
.login .login-page .register span a {
  font-size: 12px;
  color: #6982ff;
}
.active {
  border-bottom: 1px solid #5bba9d;
}
.login-label,.login-label2,.login-label3 {
  position: relative;
}
.login-p,.login-p2,.login-p3 {
  position: absolute;
  left: 0;
  top: 2px;
  margin: 0;
  padding: 0;
  font-size: 13px;
  color: #b3b3b3;
}
</style>




