<template>
   <div class="picture">
       <div class="picture-list">
           <div class="picture-item" v-for="(item,index) in picturelist" :key="index">
               <div class="left">
                 <div class="img">
                   <img :src="item.imgsrc" alt="" class="pic-img">
                 </div>
               </div>
               <div class="right">
                 <h1>{{item.title}}</h1>
                 <span>{{item.text}}</span>
                 <div class="picture-time">
                   <span class="time-left">创建时间：{{item.time}}</span>
                   <span class="time-right"> <router-link to="seemore">查看更多 </router-link> </span>
                   
                 </div>
               </div>
           </div>
       </div>
          <paging></paging>
   </div>
</template>
<script>
import paging  from '@/components/paging'
export default {
     name: 'picturelist',
     components: {
          paging
      },
     data() {
    return {
      picturelist:[{
           imgsrc:require('@/assets/images/图库封面/航天航空.jpg'),
           time:'2018/12/08',
           title:'太空摄影',
           text:'透过哈勃空间望远镜和钱德拉X射线天文台，一起来欣赏一下浩瀚的宇宙吧！',

      },
      {
           imgsrc:require('@/assets/images/图库封面/世界地理.jpeg'),
           time:'2017/12/08',
           title:'国家地理频道',
           text:'国家地理高清频道年度精选大图',

      },
      {
           imgsrc:require('@/assets/images/图库封面/世界历史.jpg'),
           time:'2017/12/08',
           title:'中国历史',
           text:'国家地理高清频道年度精选大图',

      }
      
      ]
    }
  },
  methods:{
  }
}
</script>
<style scoped>
  .picture{
      width: 100%;
      /* height: 100vh; */
      background: #f5f9f8;
  }
  .picture .picture-list{
      padding-top: 23px;
  }
  .picture .picture-list .picture-item{
    height: 210px;
    background: white;
    display: flex;
    margin:20px;
  }
  .picture .picture-list .picture-item .left{
    /* width: 20%; */
  }
   .picture .picture-list .picture-item .left .img{
     width: 240px;
     height: 160px;
     display: inline-block;
     padding: 25px 24px 25px 30px;
   }
  .picture .picture-list .picture-item .left .pic-img{
    width: 240px;
    height: 160px;
  }
  .picture .picture-list .picture-item .right{
    width: 80%;
  }
 .picture .picture-list .picture-item .right h1{
   font-size: 24px;
   color: #1c1c1c;
   padding-top: 37px;
   padding-bottom: 10px;
   display: block;
   margin: 0;
 }
  .picture .picture-list .picture-item .right span{
    font-size: 13px;
    color: #4d4d4d;
  }
.picture .picture-list .picture-item .right .picture-time{
  display: block;
  padding-top: 56px;
  padding-right: 33px;
  overflow: hidden;
}
.picture .picture-list .picture-item .right .picture-time a{
  text-decoration: none;
  font-size: 13px;
  color: #7ebdf4
}
.picture .picture-list .picture-item .right .picture-time span{
  font-size: 13px;
  color: #808080;
}
.picture .picture-list .picture-item .right .picture-time .time-left{
  float: left;
}
.picture .picture-list .picture-item .right .picture-time .time-right{
  float:right;
}

</style>



