<template>
  <div class="nav">
    <div class="nav-img">
      <img src="../assets/images/1-1/2.png" alt width="126px" height="30px">
    </div>
    <div class="nav-center">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="首页" name="sy" ></el-tab-pane>
        <el-tab-pane label="图片全库" name="pic"></el-tab-pane>
        <el-tab-pane label="展览区" name="display"></el-tab-pane>
        <el-tab-pane label="意见反馈" name="sugg"></el-tab-pane>
        <el-tab-pane label="联系我们" name="call"></el-tab-pane>
      </el-tabs>
    </div>
    <div class="nav-right">
      <div class="nav-search">
        <img src="../assets/images/2-1/ico-01-search at Tab.png" alt>
      </div>
      <div class="nav-lang">
        <img src="../assets/images/2-1/ico-02-English mode at Tab.png" alt>
      </div>
      <el-dropdown class="nav-drop" trigger="click">
        <div>
          <img class="img-setting" src="../assets/images/2-1/ico-04-setting at Tab.png" alt>
        </div>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item @click.native="collection">
            <img src="../assets/images/2-1/ico-05-collecting.png" alt>
            <span>我的收藏</span>
          </el-dropdown-item>
          <el-dropdown-item @click.native="cookies">
            <img src="../assets/images/2-1/ico-06-history.png" alt>
            <span>浏览记录</span>
          </el-dropdown-item>
          <el-dropdown-item divided @click.native="login">
            <img src="../assets/images/2-1/ico-07-log in.png" alt>
            <span>登录</span>
          </el-dropdown-item>
          <el-dropdown-item @click.native="register">
            <img src="../assets/images/2-1/ico-08-sign in.png" alt>
            <span>注册</span>
          </el-dropdown-item>
          <el-dropdown-item divided v-popover:popover4>
            <img src="../assets/images/2-1/ico-09-password.png" alt>
            <span>修改密码</span>
          </el-dropdown-item>
          <el-dropdown-item>
            <img src="../assets/images/2-1/ico-10-log out.png" alt>
            <span>退出</span>
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <el-popover ref="popover4" placement="right" width="316" top="88px" trigger="click">
      <el-form ref="form" :model="form" label-width="80px" :label-position="labelPosition">
        <h2>修改密码</h2>
        <div class="close">
          <img src="../assets/images/2-4/btn-01-close.png" alt="">
        </div>
        <el-form-item label="当前密码:" class="navbar-item">
          <el-input type="password" v-model="form.pass" class="navbar-input"></el-input>
        </el-form-item>
        <el-form-item label="新密码:" class="navbar-item">
          <el-input type="password" v-model="form.changepass" class="navbar-input"></el-input>
        </el-form-item>
        <el-form-item label="重复新密码:" class="navbar-item">
          <el-input type="password" v-model="form.checkpass" class="navbar-input"></el-input>
        </el-form-item>
        <el-form-item class="navbar-item">
          <el-button>确定</el-button>
        </el-form-item>
      </el-form>
    </el-popover>
  </div>

</template>
<script>
export default {
  name: "navbar",
  data() {
    return {
      activeName: "sy",
      labelPosition: "top",
      form: {
        pass: "",
        changepass: "",
        checkpass: ""
      }
    };
  },
  methods: {
    handleClick(tab, event) {
      //  console.log(tab, event);
      // console.log(event);
      // console.log(this.activeName);

      // console.log(event.target.innerHTML)
      //   if(event.target.innerHTML=='首页'){
      //      this.$router.push({
      //       path: "/home"
      //     });
      //   }
      // else if (event.target.innerHTML=='图片全库') {
      //   this.activeName = "pic"
      //        this.$router.push({
      //       path: "/photogallery"
      //     });
      //   }
      switch (tab.$options.propsData.name) {
        case "sy":
          this.$router.push({
            path: "/home"
          });
          break;
        default:
          this.$router.push({
            path: "/home"
          });
      }
      switch (tab.$options.propsData.name) {
        case "display":
          this.$router.push({
            path: "/exhibition"
          });
          break;
        default:
      }
      switch (tab.$options.propsData.name) {
        case "sugg":
          this.$router.push({
            path: "/feedback"
          });

          break;
        default:
      }
      switch (tab.$options.propsData.name) {
        case "call":
          this.$router.push({
            path: "/contactus"
          });
      
          break;
        default:
      }
      switch (tab.$options.propsData.name) {
        case "pic":
          this.$router.push({
            path: "/photogallery"
          });
       
          break;
        default:
      }
    },
    login: function() {
      this.$router.push({
        path: "/"
      });
    },
    register: function() {
      this.$router.push({
        path: "/register"
      });
    },
    cookies: function() {
      this.$router.push({
        path: "/cookies"
      });
    },
    collection: function() {
      this.$router.push({
        path: "/collection"
      });
    }
  }
};
</script>
<style scoped>
.el-form {
  position: relative;
}
</style>

<style>
.nav {
  display: flex;
  flex-direction: row;
  height: 49px;
}
.nav-img {
  display: flex;
  align-items: center;
  padding: 0 18px;
}

/* 中间标签 */
.nav-center {
  margin: 0 auto;
}
.nav .el-tabs {
  padding-top: 5px;
}
.nav .el-tabs__item {
  padding: 0 24px;
  font-size: 13px;
  color: #1a1a1a;
}
.nav .el-tabs__item.is-active {
  color: #4bbf9e;
}
.nav .el-tabs__nav-wrap::after {
  display: none;
}
.nav .el-tabs__active-bar {
  display: none;
}

.nav-right {
  position: relative;
  right: 20px;
  height: 49px;
  display: flex;
  flex-direction: row;
  margin-left: 24px;
}

.nav-search,
.nav-drop,
.nav-lang {
  padding: 12px 11px 0;
  display: flex;
  justify-content: center;
  border-right: 1px solid #e5e5e5;
}

.nav-search img,
.nav-drop img,
.nav-lang img {
  width: 26px;
  height: 26px;
}
.nav-search img:hover,
.nav-drop img:hover,
.nav-lang img:hover {
  cursor: pointer;
}

.el-popper[x-placement^="bottom"] {
  margin-top: 5px;
}

.el-dropdown-menu {
  padding: 0;
}
.el-dropdown-menu__item {
  line-height: 38px;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0 45px 0 0;
  font-size: 13px;
  color: #333333;
}
.el-dropdown-menu__item--divided {
  margin: 0;
}
.el-dropdown-menu__item--divided:before {
  display: none;
}
.el-dropdown-menu__item img {
  padding: 0 10px 0 15px;
}

/* 修改密码弹框 */
.el-popover {
  height: 398px;
  top: 88px !important;
  left: 40% !important;
  margin-left: 0px !important;
  padding: 0;
}
.navbar-item {
  margin-bottom: 0;
  padding-bottom: 11px;
}

.el-form h2 {
  margin: 0;
  display: block;
  font-size: 20px;
  color: #59c2ad;
  text-align: center;
  padding-top: 24px;
  padding-bottom: 17px;
}
.close {
  position: absolute;
  top: 22px;
  right: 27px;
}
.navbar-input .el-input__inner {
  background: white;
}
.navbar-item .el-form-item__label {
  font-size: 13px;
  color: #4d4d4d;
}
.navbar-item .el-form-item__content {
  width: 260px;
  height: 40px;
  display: block;
  margin: 0 auto;
}
.el-form--label-top .el-form-item__label {
  padding: 0;
}
.el-form-item__label {
  padding-left: 28px !important;
}
.el-button {
  width: 100px;
  height: 40px;
  display: block;
  background: #59c2ad;
  margin: 0 auto;
  color: #ffffff;
  font-size: 14px;
  border-radius: 4px;
}

</style>




