<template>
  <div class="coupleback">
    <div class="coupleback-head">
      <div class="head-img">
        <span class="head-span">意见反馈</span>
        <span class="span-feedback">-Give a feedback-</span>
      </div>
    </div>
    <div class="coupleback-content">
      <el-card class="box-card">
        <div class="coupleback-bigform">
          <div class="coupleback-text">
            <p class="coupleback-p1">尊敬的用户：</p>
            <p class="coupleback-p2">感谢您使用本数据库！您的意见是促使本数据库不断改进、更趋完善的动力和目标。因此，请您根据使用过程的实际体验，在下面将您的宝贵意见反馈给我们。再次感谢您的支持！我们收到您的意见后，将尽力改善本数据库，为您提供更为完善的功能及内容。</p>
          </div>
          <el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign" class="coupleback-form">
            <el-form-item label="姓名 :" class="coupleback-item">
              <el-input v-model="formLabelAlign.name" class="coupleback-input" placeholder="请输入您的姓名"></el-input>
            </el-form-item>
            <el-form-item label="所属机构 :" class="coupleback-item">
              <el-input v-model="formLabelAlign.organization" class="coupleback-input" placeholder="请输入您的所属机构"></el-input>
            </el-form-item>
            <el-form-item label="所属院系 :" class="coupleback-item">
              <el-input v-model="formLabelAlign.department" class="coupleback-input" placeholder="请输入您的所属院系"></el-input>
            </el-form-item>
            <el-form-item label="您的身份：" class="coupleback-item">
              <el-select v-model="formLabelAlign.identity" placeholder="请选择您的身份">
                <el-option label="学生" value="student"></el-option>
                <el-option label="老师" value="teacher"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="邮箱地址 :" class="coupleback-item email">
              <el-input v-model="formLabelAlign.email" class="coupleback-input" placeholder="请输入您的邮箱地址"></el-input>
            </el-form-item>
            <div class="coupleback-option">
              <el-form-item label="您认为本数据库的图片内容对教学研究的作用 ：" class="coupleback-item option">
                <el-radio-group v-model="formLabelAlign.resource">
                  <el-radio label="极好"></el-radio>
                  <el-radio label="一般"></el-radio>
                  <el-radio label="很差"></el-radio>
                </el-radio-group>
              </el-form-item>
              <el-form-item label="您认为本数据库的文字内容对图片背景资料的反映作用 ：" class="coupleback-item option">
                <el-radio-group v-model="formLabelAlign.resource">
                  <el-radio label="极好"></el-radio>
                  <el-radio label="一般"></el-radio>
                  <el-radio label="很差"></el-radio>
                </el-radio-group>
              </el-form-item>
              <el-form-item label="您认为本数据库内容分库、专题建设等组织工作 ：" class="coupleback-item option">
                <el-radio-group v-model="formLabelAlign.resource">
                  <el-radio label="极好"></el-radio>
                  <el-radio label="一般"></el-radio>
                  <el-radio label="很差"></el-radio>
                </el-radio-group>
              </el-form-item>
              <el-form-item label="您认为本数据库的浏览、检索等功能的使用方便性 ：" class="coupleback-item option">
                <el-radio-group v-model="formLabelAlign.resource">
                  <el-radio label="极好"></el-radio>
                  <el-radio label="一般"></el-radio>
                  <el-radio label="很差"></el-radio>
                </el-radio-group>
              </el-form-item>
              <el-form-item label="综合上述使用体验，您觉得本数据库对教学研究的价值 ：" class="coupleback-item option">
                <el-radio-group v-model="formLabelAlign.resource">
                  <el-radio label="极好"></el-radio>
                  <el-radio label="一般"></el-radio>
                  <el-radio label="很差"></el-radio>
                </el-radio-group>
              </el-form-item>
              <el-form-item label="若有其他意见或建议，请在下面填写 ：" class="coupleback-item option">
                <el-input type="textarea" v-model="formLabelAlign.desc" placeholder="请输入您的意见或建议"></el-input>
              </el-form-item>
              <el-form-item class="coupleback-item option option-btn">
                <el-button type="primary" class="coupleback-button">提交</el-button>
              </el-form-item>
            </div>
          </el-form>
        </div>
      </el-card>
    </div>
  </div>
</template>
<script>
export default {
  name: "coupleback",
  components: {},
  data() {
    return {
      labelPosition: "top",
      formLabelAlign: {
        name: "",
        organization: "",
        department: "",
        identity: "",
        email: "",
        resource: "",
        desc: ""
      }
    };
  }
};
</script>
