<template>
  <div>
    <div
      class="list-item"
      @click="itemClick(model.label)"
      :class="{ labelactive: current===model.label}"
    >
      <div class="label" :style="ident" @click="gallery">{{model.label}}</div>
      <span class="hot" v-if="model.label=='热门图片'">hot</span>
      <img
        class="tog-img"
        v-show="model.children && showChildren"
        @click.stop="toggleChildren"
        src="../assets/images/3-1/btn-08-close.jpg"
        alt
      >
      <img
        class="tog-img"
        v-show="model.children && !showChildren"
        @click.stop="toggleChildren"
        src="../assets/images/3-1/btn-07-open.jpg"
        alt
      >
    </div>
    <mytree
      v-show="showChildren"
      v-for="(item,index) in model.children"
      :model="item"
      :key="index"
      :depth="depth+1"
    ></mytree>
  </div>
</template>
<script>
import pictureLib from "@/components/pictureLib";
export default {
  name: "mytree",
   components: {
    pictureLib 
  },
  props: ["model", "depth",],
  data() {
    return {
      showChildren: false,
      isActive: false,
      current: ""
    };
  },
  computed: {
    ident() {
      return { "padding-left": `${this.depth * 5}px` };
    }
  },
  watch: {
    itemClick(val, oldVal) {
      console.log("wew", val, oldVal);
    }
  },
  methods: {
    toggleChildren() {
      this.showChildren = !this.showChildren;
    },
    itemClick(item) {
      this.current = "";
      this.$nextTick(() => {
        var aEls = document.getElementsByClassName("list-item");
        for (var i = 0; i < aEls.length; i++) {
          if (aEls[i].classList[1] == "labelactive") {
            aEls[i].classList.remove("labelactive");
          }
        }
        this.current = item;
        //  console.log(aEls[1].classList);
      });
      this.isActive = true;
    },
    /*点击侧边分栏时候
      瀑布流隐藏
      分库列表显示
     */
    gallery:function(){
     $(".container-water-fall").css("display","none");
     $(".gallery").css("display","block");
     $("el-footer").css("display","none");
    }
  }
};

</script>
<style>
.fade-enter-active {
  transition: all 0.8s ease;
}
.fade-leave-active {
  transition: all 0.3s ease;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
.list-item {
  display: flex;
  flex-direction: row;
  padding: 8px 17px 8px 18px;
}
.label {
  width: 197px;
  cursor: pointer;
  box-sizing: border-box;
  color: #4d4d4d;
  font-size: 12px;
  line-height: 16px;
}
.label:hover {
  color: #1a1a1a;
}
.labelactive {
  padding-top: 6px;
  padding-bottom: 6px;
  background-color: #f2f2f2;
}
.hot {
  font-size: 12px;
  color: #dc3131;
}
.list-item img {
  cursor: pointer;
  width: 16px;
  height: 16px;
}
</style>



