<template>
    <div class="skim">
        <div class="img-list">
            <div class="img-item" v-for="(item,index) in imgList" :key="index">
                <img v-bind:src="item.url" alt="">
                <span class="skim-span">{{item.text}}</span>
            </div>
        </div>
    </div>
</template>
<script>
export default {
  name: "skim",
  data() {
    return {
      imgList: [
        { url: require("../assets/images/图库封面/世界历史.jpg"),
          text:"在瑞典，即使是不识字的孩童也..."
        },
        { url: require("../assets/images/图库封面/世界地理.jpeg"),
          text:"四季很好，如果你在。"
        },
        { url: require("../assets/images/图库封面/世界建筑.jpeg"),
         text:"路过的风景，有没有..."
        },
        { url: require("../assets/images/图库封面/世界文化遗产.jpg"),
           text:"青春如此美丽，以致..."
        },
        { url: require("../assets/images/图库封面/世界自然遗产.jpg"),
           text:"我在岁月里等你经过"
        },
        { url: require("../assets/images/图库封面/中华医药.jpg"),
           text:"只要有花可开，就不..."
        },
        { url: require("../assets/images/图库封面/中国历史.jpg"),
         text:"我们这一生，会遇到..."
        },
        { url: require("../assets/images/图库封面/中国地理.jpeg"),
         text:"隔一程山水，你是我..."
        },
      ]
    };
  }
};
</script>
<style scoped>
.skim .img-list {
  width: 100%;
  overflow: hidden;
  display: inline-block;
  text-align: center;
  /* padding-bottom: 95px; */
}
.skim .img-list .img-item {
  width: 300px;
  /* float: left; */
  display: inline-block;
  padding: 16px;
  position: relative;
}
.skim .img-list .img-item img {
  width: 300px;
  height: 298px;
}
.skim-span{
    position: absolute;
    left: 24px;
    bottom: 40px;
    color: #ffffff;
}
</style>





