<template>
  <div class="collect">
    <div class="img-list">
      <div class="img-item" v-for="(item,index) in imgList" :key="index">
        <img v-bind:src="item.url" alt="">
        <span class="collect-span">{{item.text}}</span>
        <div class="collect-like" v-if="item.icon">
          <a href="#">
            <img v-bind:src="item.icon" alt="" class="collect-icon">
          </a>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "collect",
  data() {
    return {
      imgList: [
        {
          url: require("../assets/images/图库封面/世界历史.jpg"),
          text: "在瑞典，即使是不识字的孩童也...",
          icon:require("../assets/images/8-1/btn-01-sel-like.png")
        },
        {
          url: require("../assets/images/图库封面/世界地理.jpeg"),
          text: "四季很好，如果你在。"
        },
        {
          url: require("../assets/images/图库封面/世界建筑.jpeg"),
          text: "路过的风景，有没有..."
        },
        {
          url: require("../assets/images/图库封面/世界文化遗产.jpg"),
          text: "青春如此美丽，以致...",
          icon:require("../assets/images/8-1/btn-01-sel-like.png")
        },
        {
          url: require("../assets/images/图库封面/世界自然遗产.jpg"),
          text: "我在岁月里等你经过"
        },
        {
          url: require("../assets/images/图库封面/中华医药.jpg"),
          text: "只要有花可开，就不...",
          icon:require("../assets/images/8-1/btn-01-sel-like.png")
        },
        {
          url: require("../assets/images/图库封面/中国历史.jpg"),
          text: "我们这一生，会遇到...",
          icon:require("../assets/images/8-1/btn-01-sel-like.png")
        },
        {
          url: require("../assets/images/图库封面/中国地理.jpeg"),
          text: "隔一程山水，你是我..."
        }
      ]
    };
  }
};
</script>
<style scoped>
.collect .img-list {
  width: 100%;
  overflow: hidden;
  display: inline-block;
  text-align: center;
  /* padding-bottom: 95px; */
}
.collect .img-list .img-item {
  width: 300px;
  /* float: left; */
  display: inline-block;
  padding: 16px;
  position: relative;
}
.collect .img-list .img-item img {
  width: 300px;
  height: 298px;
}
.collect-span {
  position: absolute;
  left: 24px;
  bottom: 40px;
  color: #ffffff;
}
.collect .img-list .img-item .collect-like {
  width: 34px;
  height: 34px;
  display: inline-block;
  background: #000000;
  opacity: 0.5;
  border-radius: 50%;
  position: absolute;
  bottom: 31px;
  right: 36px;
}
.collect .img-list .img-item .collect-like .collect-icon {
  width: 34px;
  height: 34px;
}
</style>





