<template>
  <div class="paging">
    <el-pagination background layout="prev, pager, next,jumper" :page-count="20">
    </el-pagination>
  </div>
</template>
<script>
export default {
  name: "paging",
  data() {
    return {};
  },

  mounted: function() {
    this.span(); //需要触发的函数
  },
  methods: {
    span: function() {}
  }
};
</script>
<style scoped>
.paging {
  padding-top: 24px;
  padding-bottom: 36px;
}
.el-pagination {
  text-align: center;
}
</style>
<style>
.el-pagination li.active {
  background-color: #6acdb9 !important;
}
.btn-prev,
.btn-next {
  width: 40px !important;
  height: 36px !important;
  font-size: 13px;
  color: #b2b2b2;
  border-radius: 3px !important;
}
.el-pagination.is-background .el-pager li {
  width: 40px;
  height: 36px;
  padding: 0;
  font-size: 13px;
  color: #b2b2b2;
  line-height: 36px;
}
.el-pagination.is-background .btn-next,
.el-pagination.is-background .btn-prev,
.el-pagination.is-background .el-pager li {
  background-color: #e1e2e6;
}
.el-icon-arrow-right {
  color: #b2b2b2;
}
.paging .el-pagination__jump {
  font-size: 0px !important;
}
.el-pagination__editor.el-input .el-input__inner{
  width: 40px !important;
  height: 36px !important;
}
.el-pagination__jump{
  margin-left:0 !important;
}
.paging .el-input__inner{
  background-color: #FFF!important;
  color: #606266 !important;
}
</style>



