<template>
  <div class="pictureset">
    <div class="pictureset-head">
      <div class="head-img">
        <span class="head-span">兔子摄影图片集</span>
      </div>
    </div>
    <div class="pictureset-content">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span class="card-span">创建时间：2017/12/08</span>
         <span class="span-a"><a href="">分享</a></span>
        </div>
        <div class="pictureset-text">
          <p>复活节即将到来，这意味着兔子和鸡蛋讲随处可见(至少是巧克力品种)。</p>
          <p>说到兔子到处都是，世界上没有其他地方比日本的熬野岛更真实了。这个位于日语广岛县的小岛，估计有10000只兔子-这个岛通常被称为Usage Jima——意思是“兔子岛”.</p>
        </div>
        <div class="pictureset-img">
          <img src="../assets/images/测试用图片/新建文件夹/rabbit1.png" alt="">
          <h5>从后面野生家兔(Oryctolagus cuniculus)视图，Okunojima嗨岛，亦称兔子岛，广岛，日本，5月.</h5>
        </div>
        <div>
          <p>关于他们如何首先到达那里有点令人费解 - 有些人认为他们是从日本人用作实验化学武器的兔子下来的，但这些兔子显然过得很安稳。其他人认为他们是被一对英国夫妇偷运进来的。另一个理论是他们被附近的一所学校释放。不管如何，他们到了那里，然后就留在那里。由于没有天生的捕食者来控制他们的数量，他们的数量不断增长了，自由奔跑，就像他们拥有的这个地方一样。</p>
          <p>岛上作为景点免费向游客开放，您甚至可以购买食物来喂养它们。摄影师Yukihiro Fukuda前往该岛参观。他可爱的照片精美地捕捉了兔子在他们的环境中自由地生活-与我们经常看到的作为宠物的兔子形成鲜明对比。</p>
        </div>
        <div class="pictureset-imglist">
           <div class="pictureset-imgitem" v-for="(img,index) in picimgList" :key="index">
              <img v-bind:src="img.url" alt="">
           </div>
        </div>
      </el-card>
    </div>
  </div>
</template>
<script>
export default {
  name: "pictureset",
  components: {},
  data() {
    return {
       picimgList:[
        {url:require('../assets/images/测试用图片/新建文件夹/rabbit3.png')},
        {url:require('../assets/images/测试用图片/新建文件夹/rabbit4.png')},
        {url:require('../assets/images/测试用图片/新建文件夹/rabbit2.png')},
        {url:require('../assets/images/测试用图片/新建文件夹/rabbit3.png')},
        {url:require('../assets/images/测试用图片/新建文件夹/rabbit4.png')},
        {url:require('../assets/images/测试用图片/新建文件夹/rabbit2.png')},
      ]
    };
  }
};
</script>
<style scoped>

.pictureset .pictureset-head .head-img {
  width: 100%;
  height: 700px;
  display: block;
  background-image: url("../assets/images/测试用图片/新建文件夹/rabbit1.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: relative;
}
.pictureset .pictureset-head .head-img .head-span {
  font-size: 48px;
  color: #ffffff;
  position: absolute;
  top: 211px;
  left: 50%;
  transform: translate(-50%, -50%);
}

.pictureset .pictureset-content {
  background-color:#F5F9F8;
  height: 1495px;
}
.pictureset .pictureset-content .box-card {
  position: relative;
  width: 1216px;
  display: block;
  margin: 0 auto;
  top: -144px;
  /* left: 16px; */
  z-index: 99;
  box-shadow: 0 -2px 10px rgba(73,73,73,0.15);
  /* height: 1524px; */
}
.clearfix{
  display: flex;
}
.el-card__body{
  padding: 0;
}
.card-span{
  font-size: 13px;
  color: #808080;
  padding: 43px 0 26px 70px;
  display: block;
  flex: 1;
}
.span-a{
  padding: 43px 70px 26px 0px;
}
.span-a a{
  text-decoration: none;
  font-size: 13px;
  color:#317cda;
}
p{
   margin: 0;
  padding-top: 34px;
  padding-left: 70px;
  color: #1a1a1a;
  font-size: 15px;
  line-height: 20px;
  width: 1020px;
}
.pictureset-img{
  display: block;
  text-align: center;
  padding-top: 36px;
}
.pictureset-img img{
  width: 600px;
  height: 400px;
}
h5{
  margin: 0;
  display: block;
  padding-top: 7px;
  font-size: 12px;
  color: #808080;

}
.pictureset-imglist{
  width: 100%;
  overflow: hidden;
  display: inline-block;
  text-align: center;
  padding-top: 32px;
  padding-bottom: 95px;
}
.pictureset-imglist .pictureset-imgitem{
  width: 345px;
  display: inline-block;
  padding: 10px;
}
.pictureset-imglist .pictureset-imgitem img{
  width: 345px;
  height: 230px;
}

</style>
<style>
.el-card__header{
  padding: 0 ;
  border-bottom: 1px solid #cccccc;
}
.el-card__body{
  padding:0 !important;
}
</style>




