<template>
  <div class="contact">
    <div class="contact-head">
      <div class="head-img">
        <span class="head-span">联系我们</span>
        <span class="span-feedback">-Contact Us-</span>
      </div>
    </div>
    <div class="contact-content">
      <el-card class="box-card">
        <div class="contact-bigform">
          <div class="contact-presales">
            <h1>售前联系人</h1>
            <div class="contact-list">
              <div class="contact-item" v-for="(item,index) in presales" :key="index">
                <p>{{item.name}}</p>
                <p>{{item.number}}</p>
                <p>{{item.telephone}}</p>
                <a href="#">复制</a>
              </div>
            </div>
          </div>
          <div class="contact-aftersale">
            <h1>售后联系人</h1>
            <div class="contact-list">
              <div class="contact-item" v-for="(item,index) in aftersale" :key="index">
                <p>{{item.name}}</p>
                <p>{{item.number}}</p>
                <p>{{item.telephone}}</p>
                <a href="#">复制</a>
              </div>
            </div>
          </div>
        </div>
      </el-card>
    </div>
  </div>
</template>
<script>
export default {
  name: "contact",
  components: {},
  data() {
    return {
      presales: [
        {
          name: "木木",
          number: "+86 028 65176008",
          telephone: "+86 028 65197008"
        },
        {
          name: "木木",
          number: "+86 028 65196008",
          telephone: "+86 028 65196008"
        },
        {
          name: "木木",
          number: "+86 028 65196008",
          telephone: "+86 028 65196008"
        }
      ],
      aftersale: [
        {
          name: "木木",
          number: "+86 028 65178008",
          telephone: "+86 028 65176008"
        },
        {
          name: "木木",
          number: "+86 028 65176008",
          telephone: "+86 028 65176008"
        }
      ]
    };
  }
};
</script>
<style scoped>
.contact .contact-head .head-img {
  width: 100%;
  height: 700px;
  display: block;
  background-image: url("../assets/images/测试用图片/新建文件夹/cat.jpg");
  background-repeat: no-repeat;
  /* background-size: 100% 100%; */
  position: relative;
}
.contact .contact-head .head-img .head-span {
  font-size: 48px;
  color: #ffffff;
  font-weight: bold;
  position: absolute;
  top: 211px;
  left: 50%;
  transform: translate(-50%, -50%);
}
.contact .contact-head .head-img .span-feedback {
  font-size: 22px;
  color: #ffffff;
  position: absolute;
  top: 256px;
  left: 50%;
  transform: translate(-50%, -50%);
}
.contact-content {
  background-color: #f5f9f8;
  height: auto;
}
.contact .contact-content .box-card {
  position: relative;
  width: 1216px;
  display: block;
  margin: 0 auto;
  top: -144px;
  z-index: 99;
  height: auto;
}
.contact-bigform {
  padding: 0px 192px;
}
/* 售前 */
.contact-presales h1 {
  margin: 0;
  font-size: 24px;
  color: #1a1a1a;
  display: block;
  padding: 52px 0px;
  font-weight: bold;
}
.contact-presales .contact-list {
  width: 100%;
  overflow: hidden;
}
.contact-presales .contact-list .contact-item {
  float: left;
  width: 50%;
  text-align: left;
}
.contact-presales .contact-list .contact-item:nth-child(2) {
  text-align: right;
}
.contact-presales .contact-list .contact-item:nth-child(2) p:first-child {
  margin-right: 110px;
}
.contact-presales .contact-list .contact-item:nth-child(2) a {
  margin-right: 110px;
}

.contact-presales .contact-list .contact-item:last-child {
  padding-top: 43px;
}
.contact-presales .contact-list .contact-item p {
  margin: 0;
  font-size: 16px;
  color: #1a1a1a;
  line-height: 24px;
}

.contact-presales .contact-list .contact-item a {
  text-decoration: none;
  font-size: 16px;
  color: #5496c5;
}
/* 售后 */
.contact-aftersale {
  padding-bottom: 60px;
}
.contact-aftersale h1 {
  margin: 0;
  font-size: 24px;
  color: #1a1a1a;
  display: block;
  padding: 60px 0px 52px 0px;
  font-weight: bold;
}
.contact-aftersale .contact-list {
  width: 100%;
  overflow: hidden;
}
.contact-aftersale .contact-list .contact-item {
  float: left;
  width: 50%;
  text-align: left;
}
.contact-aftersale .contact-list .contact-item:last-child {
  text-align: right;
}
.contact-aftersale .contact-list .contact-item:last-child p:first-child {
  margin-right: 110px;
}
.contact-aftersale .contact-list .contact-item:last-child a {
  margin-right: 110px;
}
/* IE11兼容 */
@media screen and(-ms-high-contrast:active), (-ms-high-contrast: none) {
  .contact-presales .contact-list .contact-item:nth-child(2) p:first-child {
    margin-right: 90px;
  }
  .contact-presales .contact-list .contact-item:nth-child(2) a {
    margin-right: 90px;
  }
  .contact-aftersale .contact-list .contact-item:last-child p:first-child {
    margin-right: 90px;
  }
  .contact-aftersale .contact-list .contact-item:last-child a {
    margin-right: 90px;
  }
}
.contact-aftersale .contact-list .contact-item p {
  margin: 0;
  font-size: 16px;
  color: #1a1a1a;
  line-height: 24px;
}

.contact-aftersale .contact-list .contact-item a {
  text-decoration: none;
  font-size: 16px;
  color: #5496c5;
}
</style>
<style>
.el-card__header {
  padding: 0;
  border-bottom: 1px solid #cccccc;
}
.el-card__body {
  padding: 0 !important;
}
.contact-content .el-card__body {
  text-align: center;
  border: 1px solid #ffd821;
  margin: 44px 42px;
}
</style>




