<template>
  <el-container>
    <el-header>
      <navbar></navbar>
    </el-header>
    <el-main>
      <div class="Imagedetails">
        <div class="Imagedetails-content">
          <div class="Imagedetails-left">
           
              <img :class="{'active':isChoose}" src="../assets/images/测试用图片/新建文件夹/8.jpeg" alt="" class="Imagedetails-img" @click="imgScc">
            
          </div>
          <div class="Imagedetails-right">
            <swiper :options="swiperOption" class="Imagedetails-swiper">
              <swiper-slide v-for="(slide,index) in swiperSlides" :key="index" class="Imagedetails-slide"><img :src="slide.imgsrc" alt=""></swiper-slide>
              <div class="swiper-pagination" slot="pagination"></div>
              <div class="swiper-button-prev" slot="button-prev">
                <img src="../assets/images/4-1/btn-01-last-page.png" alt="">
              </div>
              <div class="swiper-button-next" slot="button-next">
                <img src="../assets/images/4-1/btn-02-next-page.png" alt="">
              </div>
            </swiper>
            <div class="collection-download">
              <span class="collection">收藏图片
                <a href="">
                  <img src="../assets/images/4-1/btn-03-nor-like.png" alt="">
                </a>
              </span>
              <span class="download">下载图片
                <a href="">
                  <img src="../assets/images/4-1/btn-05-download.png" alt="">
                </a>
              </span>
            </div>
            <div class="caption">
              <span>图片说明</span>
              <p>在瑞典，即使是不认识字的孩童也能一眼认出奥瑟尔大街，因为那是瑞士最为人熟知几条马路之一，既是瑞士的标志，也是瑞士的骄傲。</p>
            </div>
            <div class="pictorial-information">
              <span>图片信息</span>
              <p class="signature">作者署名：赵大龙</p>
              <p class="creation">创作时间：2018年10月</p>
            </div>
            <div class="label">
              <span>标签</span>
              <el-row>
                <el-button round>瑞士</el-button>
                <el-button round>街道</el-button>
                <el-button round>天空</el-button>
                <el-button round>马路</el-button>
                <el-button round>敲击</el-button>
                <el-button round>旅行</el-button>
                <el-button round>马路</el-button>
              </el-row>
            </div>
          </div>

        </div>

      </div>
    </el-main>
  </el-container>

</template>
<script>
import navbar from "@/components/navbar";

import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  name: "Imagedetails",
  components: {
    navbar,
    swiper,
    swiperSlide
  },
  data() {
    return {
      isChoose: false,
      swiperOption: {
        slidesPerView: 4,
        centeredSlides: true,
        spaceBetween: 30,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        }
      },
      swiperSlides: [
        {
          imgsrc:  require('@/assets/images/测试用图片/新建文件夹/1.jpg')
        },
        {
          imgsrc: require('@/assets/images/测试用图片/新建文件夹/2.jpg')
        },
        {
          imgsrc: require('@/assets/images/测试用图片/新建文件夹/3.jpeg')
        },
        {
          imgsrc: require('@/assets/images/测试用图片/新建文件夹/4.jpeg')
        },
        {
          imgsrc:require('@/assets/images/测试用图片/新建文件夹/11.jpeg') 
        },
        {
          imgsrc: require('@/assets/images/测试用图片/新建文件夹/1.jpg')
        },
        {
          imgsrc: require('@/assets/images/测试用图片/新建文件夹/1.jpg')
        },
        {
          imgsrc:require('@/assets/images/测试用图片/新建文件夹/12.jpeg') 
        }
      ]
    };
  },
  methods: {
    imgScc: function() {
      // alert("aaa")
      this.isChoose = !this.isChoose;
    },
    appendSlide() {
      this.swiperSlides.push(this.swiperSlides.length + 1);
    },
    prependSlide() {
      this.swiperSlides.unshift(this.swiperSlides[0] - 1);
    },
    popSlide() {
      this.swiperSlides.pop();
    },
    shiftSlide() {
      this.swiperSlides.shift();
    }
  }
};
</script>
<style scoped>
.el-main {
  padding: 0;
}
.Imagedetails {
  width: 100%;
  background-color: #f2f3f7;
  height: 670px;
}
.Imagedetails .Imagedetails-content {
  width: 100%;
  display: flex;
  overflow: hidden;
  padding: 26px 0px;
}
.Imagedetails .Imagedetails-content .Imagedetails-left {
  padding-left: 26px;
}
.Imagedetails .Imagedetails-content .Imagedetails-left .Imagedetails-img {
  width: 720px;
  height: 500px;
  transform: scale(1);
  transition: all ease 0.5s;
}
.Imagedetails .Imagedetails-content .Imagedetails-left .Imagedetails-img.active {
  /* transform: scale(3);  */
  position: absolute; 
  z-index: 100;
  width: 1300px;
  height: 648px;
}
.Imagedetails .Imagedetails-content .Imagedetails-right {
  margin-left: 24px;
}
.Imagedetails-right .Imagedetails-swiper .swiper-slide {
  width: 80px !important;
  margin: 0 !important;
  padding: 0px 8px;
}
.Imagedetails-right .swiper-slide img {
  width: 80px;
  height: 80px;
}
.Imagedetails-right .Imagedetails-swiper {
  width: 530px;
  margin: 0;
}
.Imagedetails-right .Imagedetails-slide {
  width: 80px !important;
}
.collection-download {
  padding: 24px 0px 26px 0px;
}
.collection-download span {
  font-size: 15px;
  color: #1a1a1a;
}
.collection-download .collection {
  margin-right: 52px;
}
.collection-download img {
  width: 16px;
  height: 16px;
}
.collection-download .collection a img {
  padding-left: 12px;
}
.collection-download .download a img {
  padding-left: 12px;
}
.caption {
  padding-bottom: 26px;
}
.caption span {
  font-size: 15px;
  color: #1a1a1a;
}
.caption p {
  margin: 0;
  padding-top: 14px;
  font-size: 13px;
  color: #808080;
  width: 470px;
}
.pictorial-information {
  padding-bottom: 26px;
}
.pictorial-information span {
  font-size: 15px;
  color: #1a1a1a;
}
.pictorial-information p {
  margin: 0;
  font-size: 13px;
  color: #808080;
}
.pictorial-information .signature {
  padding-top: 14px;
  padding-bottom: 8px;
}
.label span {
  font-size: 15px;
  color: #1a1a1a;
}
</style>
<style>
.swiper-container-android .swiper-slide,
.swiper-wrapper {
  left: -184px;
}
.Imagedetails-swiper .swiper-button-prev {
  background-image: none;
  width: 26px;
  height: 80px;
  background-color: #e1e2e6;
  top: 22px;
  left: 0;
}
.Imagedetails-swiper .swiper-button-prev img {
  width: 26px;
  height: 26px;
  margin-top: 26px;
}
.Imagedetails-swiper .swiper-button-next {
  background-image: none;
  width: 26px;
  height: 80px;
  background-color: #e1e2e6;
  top: 22px;
  right: 0;
}
.Imagedetails-swiper .swiper-button-next img {
  width: 26px;
  height: 26px;
  margin-top: 26px;
}
.Imagedetails-swiper .swiper-button-prev:hover {
  background-color: #d4d5d9;
}
.Imagedetails-swiper .swiper-button-next:hover {
  background-color: #d4d5d9;
}
.label .el-row {
  padding-top: 16px;
  width: 500px;
}
.label .el-row button {
  display: inline-block;
  background-color: #f2f3f7;
  font-size: 13px;
  color: #4d4d4d;
  width: 68px;
  height: 30px;
  border-radius: 15px;
  padding: 9px 19px;
  margin-right: 9px;
  border: 1px solid #b3b3b3;
  margin-bottom: 9px;
}
.label .el-row button:hover {
  border: 1px solid #5bba9d;
  color: #5bba9d;
}
.el-button + .el-button {
  margin-left: 0px;
}
</style>




