<template>
  <el-container>
    <div class="pic-main">
      <div class="fenku" ref="fenku" v-if="open">
        <!-- <img src="../assets/images/3-1/btn-02-close list.png" alt @click="closeList"> -->
        <h3 style="margin:22px 0 11px 18px;">分库列表</h3>
        <div>
          <div v-for="(item, index) in data1" :key="index">
            <mytree :model="item" :depth="0"></mytree>
          </div>
        </div>
      </div>
      <div class="open-fenku">
        <img v-if="!open" src="../assets/images/3-1/btn-01-open list.jpg" alt @click="openList">
        <img v-if="open" src="../assets/images/3-1/btn-02-close list.png" alt @click="closeList">
      </div>

      <div class="container-water-fall" v-show="waterfall">
        <div class="pic-searchbar">
          <div class="search">
            <img class="sear-img" src="../assets/images/2-1/ico-01-search at Tab.png" alt>
            <input v-model="input" placeholder="寻找你需要的图片">
            <a href="">
              <span class="picturelib-span">搜索</span>
            </a>
            <span class="search-setting">
              <img src="../assets/images/2-1/ico-12-setting at search.png" alt>
            </span>
          </div>
        </div>
        <waterfall :col="col" :width="296" :gutterWidth="26" :data="data" @loadmore="loadmore">
          <div class="cell-item" v-for="(item,index) in data" :key="index">
            <div class="mask" :style="{height:item.h + 'px'}" @mouseenter="enter(index)" @mouseleave="leave()" @click="imagedetails">
              <img v-if="item.img" :src="item.img" alt="" :width="item.w" :height="item.h">
              <div v-if="seen&&index==current" class="descripe">在瑞士，即使是不识字的孩童</div>
            </div>
          </div>
        </waterfall>
      </div>
      <div style="margin-left:6px;font-size:12px;color:#4d4d4d ;display:none" class="gallery">
        <div style="height:76px">
          <div style="margin:25px 0 11px 10px">
            <span style="margin-right:20px">图库名称：</span>
            <span style="margin-right:116px">世界自然遗产(总库)</span>
            <span>图片数量：</span>
            <span>223</span>
          </div>
          <div style="margin-left:10px;">
            <span>图库说明：</span>
            <span>本图库为自然遗产是联合国教科文组织为了保护自然遗产而设立的，根据《保护世界文化与自然遗产公约》规定申报成功的将列入《世界遗产名录》</span>
          </div>
        </div>
        <div class="fenku-all">
          <div class="fenku-img mask2" v-for="(item,index) in fenkuImg" :key="index" @mouseenter="enter(index)" @mouseleave="leave()" @click="photogallery">
            <img :src="item.img" alt>
            <div v-if="seen&&index==current" class="fenku-descripe">
              <span>世界自然遗产</span>
              <div class="fenku-descripe2">World Natural Heritage</div>
            </div>
            <!-- v-if="seen&&index==current" -->
            <div v-if="seen&&index==current" class="fenku-descripe3">
              <span>本图库为自然遗产是联合国教科文组织为了保护自然遗产而设立的，根据《保护世界文化与自然遗产公约》规定申报成功的将列入《世界遗产名录》</span>
            </div>
          </div>
        </div>
        <paging></paging>
        <el-footer>
          <foot></foot>
        </el-footer>
      </div>
    </div>
    <el-footer style="display:none">
      <foot></foot>
    </el-footer>
  </el-container>
</template>
<script>
import mytree from "@/components/mytree";
import foot from "@/components/footer";
import paging from "@/components/paging";
export default {
  name: "pictureLib",
  components: {
    mytree: mytree,
    foot,
    paging
  },
  data() {
    return {
      seen: false,
      input: "",
      current: 0,
      data1: [
        {
          label: "热门图片"
        },
        {
          label: "世界地理",
          children: [
            {
              label: "欧洲地理",
              children: [
                {
                  label: ""
                }
              ]
            },
            {
              label: "亚洲地理",
              children: [
                {
                  label: "西亚地理",
                  children: [
                    {
                      label: ""
                    }
                  ]
                },
                {
                  label: "中亚地理",
                  children: [
                    {
                      label: ""
                    }
                  ]
                },
                {
                  label: "东亚地理",
                  children: [
                    {
                      label: ""
                    }
                  ]
                },
                {
                  label: "南亚地理",
                  children: [
                    {
                      label: "印度"
                    }
                  ]
                },
                {
                  label: "斯里兰卡的岛屿地理与生态",
                  children: [
                    {
                      label: "  "
                    }
                  ]
                }
              ]
            }
          ]
        },
        {
          label: "世界建筑",
          children: [
            {
              label: "二级 1-1",
              children: [
                {
                  label: "三级 1-1-1"
                }
              ]
            }
          ]
        },
        {
          label: "军事",
          children: [
            {
              label: "二级 2-1"
            }
          ]
        },
        {
          label: "世界历史",
          children: [
            {
              label: "二级 2-1"
            }
          ]
        },
        {
          label: "世界文化遗产",
          children: [
            {
              label: "二级 2-1"
            }
          ]
        },
        {
          label: "世界艺术",
          children: [
            {
              label: "二级 2-1"
            }
          ]
        },
        {
          label: "世界自然遗产",
          children: [
            {
              label: "二级 2-1"
            }
          ]
        },
        {
          label: "当代中国",
          children: [
            {
              label: "二级 2-1"
            }
          ]
        }
      ],
      defaultProps: {
        children: "children",
        label: "label"
      },

      data: [
        {
          img: require('@/assets/images/测试用图片/新建文件夹/6.jpeg'),
          w: "296",
          h: "435"
        },
        {
          img:  require('@/assets/images/测试用图片/新建文件夹/11.jpeg'),
          w: "296",
          h: "240"
        },
        {
          img: require('@/assets/images/测试用图片/新建文件夹/7.jpeg'),
          w: "296",
          h: "470"
        },
        {
          img: require('@/assets/images/测试用图片/新建文件夹/9.jpeg'),
          w: "296",
          h: "435"
        },
        {
          img:require('@/assets/images/测试用图片/新建文件夹/13.jpg') ,
          w: "296",
          h: "435"
        },
        {
          img: require('@/assets/images/测试用图片/新建文件夹/10.jpeg'),
          w: "296",
          h: "435"
        },
        {
          img:require('@/assets/images/测试用图片/新建文件夹/18.jpeg'),
          w: "296",
          h: "400"
        },
        {
          img: require('@/assets/images/测试用图片/新建文件夹/15.jpg'),
          w: "296",
          h: "180"
        },
        {
          img: require('@/assets/images/测试用图片/新建文件夹/12.jpeg'),
          w: "296",
          h: "180"
        },
        {
          img: require('@/assets/images/测试用图片/新建文件夹/17.jpeg'),
          w: "296",
          h: "435"
        },
        {
          img: require('@/assets/images/测试用图片/新建文件夹/18.jpeg'),
          w: "296",
          h: "180"
        },
        {
          img: require('@/assets/images/测试用图片/新建文件夹/16.jpeg'),
          w: "296",
          h: "435"
        },
        {
          img:require('@/assets/images/测试用图片/新建文件夹/19.jpeg') ,
          w: "296",
          h: "180"
        },
        {
          img: require('@/assets/images/测试用图片/新建文件夹/20.jpeg'),
          w: "296",
          h: "435"
        },
        {
          img:require('@/assets/images/测试用图片/新建文件夹/22.jpeg'),
          w: "296",
          h: "180"
        },
        {
          img:require('@/assets/images/测试用图片/新建文件夹/21.jpeg'),
          w: "296",
          h: "435"
        }
      ],
      fenkuImg: [
        {
          img:require('@/assets/images/图库封面/航天航空.jpg') 
        },
        {
          img: require('@/assets/images/图库封面/地质.jpg') 
        },
        {
          img:require('@/assets/images/图库封面/当代中国.jpeg') 
        },
        {
          img: require('@/assets/images/图库封面/科学技术.jpg') 
        },
        {
          img: require('@/assets/images/图库封面/民族.jpg')
        },
        {
          img:require('@/assets/images/图库封面/农业.jpg') 
        },
        {
          img: require('@/assets/images/图库封面/世界地理.jpeg')
        },
        {
          img: require('@/assets/images/图库封面/世界建筑.jpeg') 
        }
      ],
      col: 0,
      fullWidth: document.documentElement.clientWidth,
      open: false, // 打开关闭左侧分库选择栏
      open1: false,
      curr: null,

      waterfall: true // 是否展示瀑布流
    };
  },
  created() {
    window.addEventListener("resize", this.handleResize);
    this.checkCol();
  },
  watch: {
    fullWidth(newVal) {
      this.fullWidth = newVal;
      this.checkCol();
    }
  },
  beforeDestroy: function() {
    window.removeEventListener("resize", this.handleResize);
  },
  computed: {
  },

  methods: {
    handleNodeClick(data) {
    },
    openImg(index) {
      this.open1 = true;
      this.curr = index;
    },
    closeImg(index) {
      this.open1 = true;
      this.curr = index;
    },
    checkCol() {
      this.$nextTick(() => {
        if (this.$refs.fenku) {
          var fenkuWidth = this.$refs.fenku.clientWidth;
          if (
            this.fullWidth > 953 + fenkuWidth &&
            this.fullWidth < 1200 + fenkuWidth
          ) {
            this.col = 3;
          }
          if (this.fullWidth > 1200 + fenkuWidth) {
            this.col = 4;
          }
          if (this.fullWidth < 950 + fenkuWidth) {
            this.col = 2;
          }
        } else {
          if (this.fullWidth > 953 && this.fullWidth < 1200) {
            this.col = 3;
          }
          if (this.fullWidth > 1200) {
            this.col = 4;
          }
          if (this.fullWidth < 950) {
            this.col = 2;
          }
        }
      });
    },
    handleResize(event) {
      this.fullWidth = document.documentElement.clientWidth;
    },

    loadmore(index) {
      this.data = this.data.concat(this.data);
    },
    enter(index) {
      this.seen = true;
      this.current = index;
    },
    leave() {
      this.seen = false;
      this.current = null;
    },
    imagedetails() {
      this.$router.push({
        path: "/Imagedetails"
      });
    },
    photogallery() {
      $(".gallery").css("display", "none");
      $(".container-water-fall").css("display", "block");
    },
    openList() {
      this.open = true;
      this.checkCol();
      $(".search").css("margin", "0");
      $(".pic-searchbar").css("padding", "13px 0px 37px 32px");
      $(".fenku-all").css("width", "1100px");
    },
    closeList() {
      this.open = false;
      this.checkCol();
      $(".search").css("margin", "0 auto");
      $(".pic-searchbar").css("padding", "13px 0px 37px 0px");
      $(".fenku-all").css("width", "1300px");
    }
  }
};
</script>

<style>
.pic-main {
  display: flex;
  flex-direction: row;
  background-color: #f5f9f8;
}
.open-fenku {
  width: 12px;
  height: 91px;
  background-color: #ffffff;
  box-shadow: 0px 2px 4px rgba(73, 73, 73, 0.1);
  display: flex;
}
.open-fenku img {
  display: inline-block;
  cursor: pointer;
  /* padding: 2px; */
  margin: auto 0;
  /* height: 16px; */
}
.fenku {
  background-color: #ffffff;
  width: 215px;
  box-sizing: border-box;
  height: 700px;
}

/* 分库列表 */

.el-icon-caret-right:before {
  content: "";
}
.el-icon-caret-right::after {
  content: "\e791";
}

/* 瀑布流 */
.container-water-fall {
  margin-left: 26px;
  padding-top: 24px;
  padding-left: 4px;
  width: 100%;
}
.cell-item {
  padding: 0;
  margin-bottom: 24px;
  /* widows: 296px; */
}

.mask,
.mask2 {
  transition: opacity 0.5s;
  opacity: 1;
  background: #000000;
  box-sizing: border-box;
  height: auto;
  padding-bottom: -1px;
  margin-bottom: -4px;
  position: relative;
}
.cell-item:hover {
  opacity: 0.7;
}
.descripe {
  position: absolute;
  bottom: 22px;
  left: 13px;
  width: 227px;
  color: #ffffff;
  font-size: 15px;
  margin-right: 56px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  /* display: none; */
}

/* 分库点击图片 */
.fenku-all {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  /* width: 1200px; */
}
.fenku-img {
  width: 296px;
  margin: 0 26px 26px 0;
  position: relative;
}
.fenku-img img {
  width: 296px;
  height: 280px;
}
.fenku-img img:hover {
  opacity: 0.7;
}
.fenku-descripe {
  font-size: 20px;
  color: #ffffff;
  position: absolute;
  top: 129px;
  /* width: 280px; */
  left: 60px;
  text-align: center;
}
.fenku-descripe2 {
  font-size: 14px;
  color: #e6e6e6;
}
.fenku-descripe3 {
  position: absolute;
  top: 176px;
  left: 160px;
  font-size: 14px;
  /* color: #e6e6e6; */
  padding: 6px 10px;
  width: 340px;
  border: #e6e6e6 1px solid;
  /* height: 100px; */
  box-sizing: border-box;
  background-color: #ffffff;
  z-index: 10;
}
.fenku-descripe3 span {
  font-size: 12px;
  /* color: #e6e6e6; */
}
.mask2 {
  height: 280px;
}
.open-fenku img {
  width: 12px;
  height: 12px;
}

/* 搜索 */
.pic-searchbar {
  padding: 13px 0px 37px 0px;
}
.search {
  margin: 0 auto;
  /* padding-top: 13px; */
  /* padding: 13px 0px 37px 0px; */
  width: 872px;
  height: 50px;
  position: relative;
  display: flex;
  flex-direction: row;
  font-size: 15px;
  color: #b3b3b3;
}
.search input {
  width: 872px;
  height: 26px;
  padding: 12px 0 12px 54px;
  color: #b3b3b3;
  border-radius: 4px;
  border: #cccccc solid 1px;
  /* background-color: rgba(0, 0, 0, 0.5) ; */

  /* opacity: 0.5; */
}

/* 输入框占位符颜色 */
.search input::-webkit-input-placeholder,
.filter input::-webkit-input-placeholder {
  font-size: 15px;
  color: #b3b3b3;
}
.search input:-ms-input-placeholder,
.filter input::-ms-input-placeholder {
  font-size: 15px;
  color: #b3b3b3;
}
.search input::-moz-placeholder,
.filter input::-moz-input-placeholder {
  font-size: 15px;
  color: #b3b3b3;
}
.el-input input::-webkit-input-placeholder {
  /* font-size: 15px; */
  color: #ffffff;
}
.el-input input::-ms-input-placeholder {
  /* font-size: 15px; */
  color: #ffffff;
}
.el-input input::-moz-input-placeholder {
  /* font-size: 15px; */
  color: #ffffff;
}

.sear-img {
  position: absolute;
  top: 14px;
  left: 16px;
}
.search .picturelib-span {
  position: absolute;
  top: 14px;
  right: 74px;
  color: #1a1a1a;
  font-size: 15px;
  line-height: 26px;
}
.search-setting {
  position: absolute;
  top: 0;
  right: 14px;
  width: 26px;
  height: 50px;
  padding-left: 14px;
  display: flex;
  align-items: center;
  border-left: 1px solid #b2b2b2;
}
.search .search-span:hover,
.search-setting img:hover {
  cursor: pointer;
}
.gallery {
  margin-left: 28px !important;
}
.gallery .el-footer {
  position: absolute;
  left: 0;
}
</style>





