<template>
  <el-main>
    <div class="main">
      <div class="main-text">
        <span>在这里寻找适合你的图片</span>
      </div>
      <div class="img-list">
        <div class="img-item" v-for="(img,index) in imgList" :key="index" @mouseenter="enter(index)" @mouseleave="leave()">
          <img v-bind:src="img.url" alt="">
          <span class="mainer-title" v-show="active" v-if="active&&index==current">{{img.title}}</span>
          <span class="mainer-entitle" v-show="active" v-if="active&&index==current">{{img.entitle}}</span>
          <p class="mainer-p" v-show="active" v-if="img.text&& active&&index==current" >{{img.text}}</p>
        </div>
      </div>
    </div>
  </el-main>
</template>

<script>
export default {
  name: "mainer",
  data() {
    return {
      active: true,
      active: false,
      imgList: [
        {
          url: require('@/assets/images/图库封面/世界历史.jpg')
          // title: "世界自然遗产",
          // entitle: "Word Natural Heritage"
        },
        { url: require('@/assets/images/图库封面/世界地理.jpeg')},
        { url: require('@/assets/images/图库封面/世界建筑.jpeg')},
        { url: require('@/assets/images/图库封面/世界文化遗产.jpg') },
        {
          url: require('@/assets/images/图库封面/世界自然遗产.jpg'),
          title: "世界自然遗产",
          entitle: "Word Natural Heritage",
          text:
            "本图库为世界自然遗产是联合国教科文组织为了保护自然遗产而设立的，根据《保护世界文化与自然遗产公约》规定申报成功的将列入《世界遗产名录》。"
        },
        { url: require('@/assets/images/图库封面/文博.jpg')},
        { url:  require('@/assets/images/图库封面/中华医药.jpg')},
        { url:  require('@/assets/images/图库封面/中国历史.jpg')},
        { url:  require('@/assets/images/图库封面/中国地理.jpeg')},
        { url:  require('@/assets/images/图库封面/中国建筑.jpg')},
        { url:  require('@/assets/images/图库封面/中国民俗.jpg')},
        { url:  require('@/assets/images/图库封面/中国艺术.jpg')},
        { url:  require('@/assets/images/图库封面/中国非物质文化遗产.jpg') },
        { url:  require('@/assets/images/图库封面/军事.jpg')},
        { url:  require('@/assets/images/图库封面/农业.jpg') },
        { url:  require('@/assets/images/图库封面/动物.jpg')},
        { url: require('@/assets/images/图库封面/园艺.jpeg')},
        { url:  require('@/assets/images/图库封面/地质.jpg')},
        { url: require('@/assets/images/图库封面/农业.jpg') },
        { url:  require('@/assets/images/图库封面/宗教.jpeg') },
        { url:  require('@/assets/images/图库封面/当代中国.jpeg')},
        { url:  require('@/assets/images/图库封面/文博.jpg')},
        { url:  require('@/assets/images/图库封面/植物.jpeg') },
        { url:  require('@/assets/images/图库封面/民族.jpg') },
        { url:  require('@/assets/images/图库封面/科学技术.jpg')},
        { url: require('@/assets/images/图库封面/航天航空.jpg') },
        { url:  require('@/assets/images/图库封面/语言文学.jpg')},
        {
          url: require("../assets/images/2-1/pic-05-mosaic.jpg"),
          title: "更多..."
        }
      ]
    };
  },
  methods: {
    enter(index) {
      this.active = true;
      this.current = index;
    },
    leave() {
      this.active = false;
      this.current = null;
    }
  }
};
</script>

<style scoped>
.el-main {
  padding: 0;
}
/* 图片部分 */

.main .main-text {
  width: 508px;
  height: 90px;
  display: block;
  margin: 0 auto;
  padding-bottom: 110px;
}
.main .main-text span {
  text-align: center;
  font-size: 38px;
  color: #ffffff;
  line-height: 90px;
  padding: 0px 45px;
  background: #4bcc94;
  display: block;
}
.main .img-list {
  width: 100%;
  overflow: hidden;
  display: inline-block;
  text-align: center;
  padding-bottom: 95px;
}
.main .img-list .img-item {
  width: 295px;
  /* float: left; */
  display: inline-block;
  padding: 16px;
  position: relative;
}
.main .img-list .img-item img {
  width: 295px;
  height: 298px;
}
.main .img-list span {
  display: block;
}
.main .img-list .mainer-title {
  position: absolute;
  top: 125px;
  left: 90px;
  font-size: 23px;
  color: #ffffff;
}
.main .img-list .mainer-entitle {
  position: absolute;
  top: 160px;
  left: 80px;
  font-size: 14px;
  color: #e6e6e6;
}
.mainer-p {
  margin: 0;
  width: 293px;
  font-size: 13px;
  color: #191919;
  background-color: #ffffff;
  border: 1px solid #cccccc;
  padding: 9px 11px;
  position: absolute;
  bottom: 0;
  left: 230px;
  z-index: 99;
}
.main .img-list .img-item:last-child .mainer-title {
  font-size: 28px;
  left: 140px;
}
</style>


