<template>
  <div class="all">
    <div class="search">
      <img class="sear-img" src="../assets/images/2-1/ico-01-search at Tab.png" alt>
      <input v-model="input" placeholder="寻找你需要的图片">
      <span class="search-span">搜索</span>
      <span class="search-setting" @click="fn()">
        <img src="../assets/images/2-1/ico-12-setting at search.png" alt>
      </span>
    </div>
    <div class="search-select" v-if="willShow">
      <div>
        <span class="span1">搜索逻辑</span>
        <el-select v-model="value" class="filter-select">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
        <span class="span2">搜索范围</span>
        <el-select v-model="value" class="filter-select">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
        <span class="span3">搜索项目</span>
        <el-select v-model="value" placeholder="图片关键字" class="filter-select">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
        <div class="filter">
          <span>搜索过滤</span>
          <input v-model="inputFilter" placeholder="搜索结果含过滤关键词的部分将不显示，关键词用逗号分开。">
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
   name: 'search',
  data() {
    return {
      willShow:false,
      input: "",
      options: [
        {
          value: "选项1",
          label: "世界文化遗产"
        },
        {
          value: "选项2",
          label: "世界历史遗产"
        },
        {
          value: "选项3",
          label: "中国建筑"
        },
        {
          value: "选项4",
          label: "中国历史"
        },
        {
          value: "选项5",
          label: "外国建筑"
        },
        {
          value: "选项6",
          label: "外国建筑"
        },
        {
          value: "选项7",
          label: "外国建筑"
        },
        {
          value: "选项8",
          label: "外国建筑"
        }
      ],
      value: "",
      inputFilter: ""
    };
  },
   methods:{
      fn:function(){
          if(this.willShow==true){
              this.willShow=false;
          }else{
              this.willShow=true
          }
      }
  }
};
</script>
<style scoped>
.all {
  /* background-size:100% 300px;
  height: 300px; */
}

/* 搜索框 */
.search {
  margin: 0 auto;
  margin-top: 37px;
  width: 872px;
  height: 50px;
  position: relative;
  display: flex;
  flex-direction: row;
  font-size: 15px;
  color: #b3b3b3;
}
.search input {
  width: 872px;
  height: 26px;
  padding: 12px 0 12px 54px;
   color: #b3b3b3;
  border-radius: 4px;
  border: #b2b2b2 solid 1px;
  background-color: rgba(0, 0, 0, 0.5) ;
  
  /* opacity: 0.5; */
}

/* 输入框占位符颜色 */
.search input::-webkit-input-placeholder,
.filter input::-webkit-input-placeholder {
  font-size: 15px;
  color: #b3b3b3;
}
.search input:-ms-input-placeholder,
.filter input::-ms-input-placeholder {
  font-size: 15px;
  color: #b3b3b3;
}
.search input::-moz-placeholder,
.filter input::-moz-input-placeholder {
  font-size: 15px;
  color: #b3b3b3;
}
.el-input input::-webkit-input-placeholder {
  /* font-size: 15px; */
  color: #ffffff;
}
.el-input input::-ms-input-placeholder {
  /* font-size: 15px; */
  color: #ffffff;
}
.el-input input::-moz-input-placeholder {
  /* font-size: 15px; */
  color: #ffffff;
}

.sear-img {
  position: absolute;
  top: 14px;
  left: 16px;
}
.search .search-span {
  position: absolute;
  top: 14px;
  right: 74px;
  line-height: 26px;
}
.search-setting {
  position: absolute;
  top: 0;
  right: 14px;
  width: 26px;
  height: 50px;
  padding-left: 14px;
  display: flex;
  align-items: center;
  border-left: 1px solid #b2b2b2;
}
.search .search-span:hover,
.search-setting img:hover {
  cursor: pointer;
}


/* 中间select */
.search-select {
  margin: 21px auto 0;
  width: 872px;
  /* height: 124px ; */
  height: 132px;
  box-sizing: border-box;
  padding: 21px 0 21px 21px;
  color: #b3b3b3;
  font-size: 14px;
  background-color: rgba(0, 0, 0, 0.5) ;
  /* opacity: rgba(0,0,0,0.5); */
}

.search-select,
.search-select input {
  border-radius: 4px;
  border: #b2b2b2 solid 1px;
}
.span1,
.span2,
.span3 {
  margin-right: 22px;
  line-height: 34px;
}
.search-select .filter-select {
  width: 150px;
  height: 34px;
  line-height: 34px;
}

.span2,
.span3 {
  margin-left: 24px;
}


/* 过滤文字 */
.filter {
  margin: 14px 0 21px;
}
.filter input {
  margin-left: 22px;
  width: 657px;
  height: 34px;
  /* padding: 12px 0 12px 54px; */
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.1) ;
  color: #b3b3b3;
  padding-left: 12px;
  /* border: #b2b2b2 solid 1px; */
}

/* 下拉选择框的背景等 */

.el-select-dropdown__item {
  color: #ffffff;
}
.el-select-dropdown__item:hover {
  background-color: rgba(0, 0, 0, 0.8);
}


</style>
<style>
.el-input__inner {
  background-color: rgba(0, 0, 0, 0.1) ;
  color: #ffffff;
}
.el-select-dropdown {
  background-color: rgba(0, 0, 0, 0.5) ;
}

/* 滚动条颜色 宽度*/
.el-scrollbar__thumb,
.el-scrollbar__thumb:hover {
  background-color: #ffffff;
}
.el-scrollbar__bar.is-vertical {
  width: 1px;
}
.el-scrollbar__thumb {
  height: 20%;
}
</style>






