<template>
  <el-header>
  <div class="header">
     <search></search>
    <div class="block slideshow-block">
       <el-carousel :autoplay="false" trigger="click" :height="bannerHeight + 'px'">
         <el-carousel-item  v-for="(img,index) in imgList" :key="index">
           <img v-bind:src="img.url" ref="bannerHeight" @load="imgload" class="carousel">
             <div class="text">
               <a href="">图片详情</a>
               </div>
         </el-carousel-item>
       </el-carousel>
    </div>
    <div class="content">
       <div class="content-text">
         <span>专业的数字化图像资源图书馆</span>
       </div>
       <div class="center">
         <div class="left">
           <img src="../assets/images/2-1/pic-03-on-the-left.jpg" alt="" class="left-img">
         </div>
         <div class="middle">
           <h1>业务内容</h1>
            <h4>Business content</h4>
            <div class="middle-text">
              <span>公元图片数据库(以下简称"本数据库" )正是因应高等教研机构及公共服务机构</span>
              <span>信息数字化的需要而专门研发的一个专业化数据库系统。</span>
              <span>作为首个专业的图片数据库，本数据库基于最先进的计算机.数据库和网络技术,为各类</span>
              <span>图书馆及教育.研究机构提供了一大批可资永续利用的数字化图像资源,跟重要的是提供</span>
              <span>了一套对多年收集累计的已有图像资源进行数字化整理、管理及检索、应用等的一整套解</span>
              <span>决方案,为"数字化图书馆"的建设填补了一项重要空白。</span>
              <span>公元图片数据库还配备有以发布视觉传播为主题的线下活动社区,为各等高教研机构及公</span>
              <span>共服务机构提供线下活动交流及传播平台</span>
            </div>
            <div class="contact">
              <a href="">联系我们</a>
            </div>
         </div>
         <div class="right">
            <img src="../assets/images/2-1/pic-04-on-the-right.jpg" alt="" class="right-img">
         </div>
       </div>
    </div>
  </div>
  </el-header>
</template>

<script>
import 'element-ui/lib/theme-chalk/index.css'
import search from '@/components/search'
export default {
  name: 'slideshow',
   components: {
        search
      },
  data () {
    return {
      bannerHeight:'',
        imgList:[
        {url:require('@/assets/images/images/banner-02.jpg')},
        {url:require('@/assets/images/images/banner-04.jpeg')},
        {url:require('@/assets/images/images/banner-05.jpeg')},
        {url:require('@/assets/images/images/banner-03.jpeg')},
        {url:require('@/assets/images/images/banner-01.jpg')}
      ]
    }
  },
   methods:{
     imgload(){
       this.$nextTick(()=>{
         this.bannerHeight = this.$refs.bannerHeight[0].height
        //  console.log('s',this.$refs.bannerHeight[0].height)

       })
     },
     mounted(){
       this.imgload();
       window.addEventListener('resize',()=>{
          this.bannerHeight = this.$refs.bannerHeight[0].height
           this.imgload()
       },false)
     }
  },
}

</script>

<style >
/* search定位 */
.header{
  position: relative;
}
.all{
  position: absolute;
  z-index: 99;
  left: 0;
  right: 0;
}
.el-header{
  height:auto !important;
  padding:0px;
}
/* 轮播图部分 */
.carousel{
  width: 100%;
  height: inherit;
  min-height: 540px;
  min-width: 1400px;
  /* background-size: 100% 100%;
  background-repeat: no-repeat; */
}
.el-carousel__indicators--horizontal{
  bottom: 42px;
  padding-left: 20px;
}
.el-carousel__button{
  width: 14px;
  height: 14px;
  border-radius: 50%;
  opacity: 0.5;
}
.el-carousel__arrow{
  width: 52px;
  height: 80px;
  border-radius: 0;
}
.slideshow-block .el-icon-arrow-left{
  background-image: url(../assets/images/2-1/ico-14-lastbanner.png) ;
  width: 52px;
  height: 80px;
}
.slideshow-block .el-icon-arrow-left::before{
 content: " ";
}
.slideshow-block .el-icon-arrow-right::before{
 content: " ";
}
.slideshow-block .el-icon-arrow-right{
  background-image: url(../assets/images/2-1/ico-13-nextbanner.png) ;
  width: 52px;
  height: 80px;
}
.text{
  position: absolute;
  bottom: 0;
  left: 0;
}
.text a{
  font-size: 18px;
  color: #ffffff;
  display: block;
  padding-left: 46px;
  padding-bottom: 46px
}
/* 数字化部分 */
.header .content{
    padding-top: 75px;
}
.header .content .content-text{
  width:584px;
  height: 90px;
  display: block;
  background: #4bcc94;
  margin: 0 auto;
}
.header .content .content-text span{
  text-align: center;
  font-size: 38px;
  color: #ffffff;
  /* line-height: 38px;
  display: inline-block;
  width: 100%; */
  /* vertical-align:middle;    */
  line-height: 90px;
  padding: 0px 45px;
}
.content .center{
  width: 100%;
  display: flex;

}
.content .center .left{
  /* flex: 1; */
  width: 26%;
  text-align: left;
  padding-left: 42px;
  padding-top: 74px;
}
.content .center .left .left-img{
 width: 249px ;
 height: 489px;
 /* min-height: 0 !important;
 min-width: 0 !important; */
}
.content .center .middle{
  /* flex: 1; */
  width: 41%;
  padding-top: 109px;
  padding-left: 42px;
}
.content .center .middle h1{
  font-weight:bold;
  font-size: 38px;
  color:#333333;
  margin: 0;
}
.content .center .middle .contact{
  display: inline-block;
  width: 106px;
  height: 45px;
  background: #ffffff;
  padding-bottom: 159px;

}
.content .center .middle .contact a{
  color: #333333;
  font-size: 15px;
  text-align: center;
  display: inline-block;
  width: 100%;
  line-height: 46px;
  text-decoration: none;
  border: 1px solid #b2b2b2;
}
.content .center .middle h4{
   font-size: 18px;
   color:#333333;
   margin: 0;
   padding-top: 15px;
}
.content .center .middle .middle-text{
  padding-top: 34px;
  padding-bottom: 52px;
}
.content .center .middle .middle-text span{
 display: inline-block;
 font-size: 14px;
 color:#333333;
 padding-top: 10px;
}
.content .center .right{
  width: 35%;
  /* flex: 1; */
  text-align: right;
  padding-right: 42px;
  padding-top: 107px;
}
.content .center .right .right-img{
 width: 219px ;
 height: 487px ;
 /* min-height: 0 !important;
 min-width: 0 !important; */
}

</style>


