<template>
  <el-row>
    <el-col class="login">
      <div class="login-page">
        <el-row :gutter="10">
          <el-col>
            <div class="head-pic">
              <img src="../assets/images/1-1/2.png" alt="">
            </div>
          </el-col>
          <el-col>
            <div class="login-list">
              <div class="item">
                <span>注册账号</span>
                <label class="register-label">
                  <el-input v-model="account" @keyup.native="acc"></el-input>
                  <p class="register-p" v-show="accout">请输入你的注册账号</p>
                </label>
              </div>
              <div class="item itema">
                <span>手机号(选填)</span>
                <label class="register-label2">
                  <el-input v-model="tel" @keyup.native="telnumber"></el-input>
                  <p class="register-p2" v-show="mobile">请输入你的手机号码</p>
                </label>
              </div>
              <div class="item itema">
                <span>密码</span>
                <label class="register-label3">
                  <el-input v-model="psd" @keyup.native="pwd"></el-input>
                  <p class="register-p3" v-show="password">请输入8位以上的密码</p>
                </label>
              </div>
              <div class="item itema">
                <span>重复密码</span>
                <label class="register-label4">
                  <el-input v-model="psdwork" @keyup.native="pwdd"></el-input>
                  <p class="register-p4" v-show="reset">请重复输入一次密码</p>
                </label>
              </div>
              <div class="item itema">
                <span>验证码</span>
                <div class="row">
                  <div class="left">
                    <label class="register-label5">
                      <el-input v-model="verification" @keyup.native="ver"></el-input>
                      <p class="register-p5" v-show="auth">请输入右侧验证码</p>
                    </label>
                  </div>
                  <div class="right"></div>
                </div>
              </div>
            </div>
          </el-col>
          <el-col>
            <div class="login-button">
              <el-button native-type="button">注册</el-button>
            </div>
          </el-col>
          <el-col>
            <div class="register">
              <span>没有账号?</span>
              <span>
                <router-link to="/">现在登录 </router-link>
              </span>
            </div>
          </el-col>
        </el-row>
      </div>
    </el-col>
  </el-row>

</template>

<script>
import "element-ui/lib/theme-chalk/display.css";
export default {
  name: "register",
  data() {
    return {
      account: "",
      tel: "",
      verification: "",
      number: "",
      psd: "",
      psdwork: "",
      accout:true,
      mobile:true,
      password:true,
      reset:true,
      auth:true
    };
  },
  methods: {
    acc() {
      if (this.account !== "") {
        this.accout = false;
      }
      if (this.account === "") {
        this.accout = true;
      }
    },
    telnumber() {
      if (this.tel !== "") {
        this.mobile = false;
      }
      if (this.tel === "") {
        this.mobile = true;
      }
    },
    pwd() {
      if (this.psd !== "") {
        this.password = false;
      }
      if (this.psd === "") {
        this.password = true;        
      }
    },
    pwdd() {
      if (this.psdwork !== "") {
        this.reset = false;
      }
      if (this.psdwork === "") {
        this.reset = true;
      }
    },
    ver() {
      if (this.verification !== "") {
        this.auth = false;
      }
      if (this.verification === "") {
        this.auth = true;
      }
    }
  }
};
</script>


<style>
.login {
  background: #5bba9d;
  height: 737px;
}
.login .login-page {
  width: 340px;
  display: block;
  background: #ffffff;
  border: #5bba9d 1px;
  border-radius: 6px;
  box-shadow: -2px 0px 14px rgba(73, 73, 73, 0.1);
  margin: 77px auto 0px auto;
}
.login .login-page .head-pic {
  width: 340px;
  height: 39px;
  padding-top: 29px;
  margin: 0 auto;
}
.login .login-page .head-pic img {
  width: 161px;
  height: 39px;
  margin: 0 auto;
  display: block;
}
.login .login-page .login-list {
  padding: 37px 40px 0px 40px;
}
.login .login-page .login-list .itema {
  padding-top: 20px;
}
.login .login-page .login-list .item span {
  color: #4d4d4d;
  font-size: 13px;
}
.login .login-page .login-list .item .el-input__inner {
  border: none;
  border-radius: 0px;
  border-bottom: 1px solid #e5e5e5;
  font-size: 13px;
  color: #1a1a1a;
  padding: 12px 0px;
  background: #ffffff;
}
.login .login-page .login-list .item .row {
  display: flex;
}
.login .login-page .login-list .item .row .left {
  padding-right: 14px;
}
.login .login-page .login-list .item .row .right {
  width: 80px;
  height: 38px;
  background: #e5e5e5;
}
.login .login-page .login-button {
  padding-top: 25px;
}
.login .login-page .login-button .el-button {
  width: 260px;
  height: 38px;
  margin: 0 auto;
  display: block;
  border-radius: 4px;
  background: #5bba9d;
}
.login .login-page .login-button .el-button span {
  font-size: 15px;
  color: #ffffff;
}
.login .login-page .register {
  width: 100%;
  text-align: center;
  padding: 23px 0px 31px 0px;
}
.login .login-page .register span {
  font-size: 12px;
  color: #b3b3b3;
}
.login .login-page .register span a {
  font-size: 12px;
  color: #6982ff;
}
.active {
  border-bottom: 1px solid #5bba9d;
}
.register-label,
.register-label2,
.register-label3,
.register-label4,
.register-label5 {
  position: relative;
}
.register-p,
.register-p2,
.register-p3,
.register-p4,
.register-p5 {
  position: absolute;
  left: 0;
  top: 2px;
  margin: 0;
  padding: 0;
  font-size: 13px;
  color: #b3b3b3;
}
</style>




